import React from 'react'
import styled from 'styled-components'

const Container = styled.div<PropsActive>`
  background: ${props => props.active ? "#E2F1F8" : "#F3F3F3"};
  border-radius: 0.625rem;
  opacity: 1;
  padding: 1.25rem 2.5rem;
  margin-right: 0.625rem;
  margin-top: 0.625rem;
  @media screen and (max-width: 768px) {
      width: 100%;
      align-items: center;
      justify-content: center;
      margin-right: 0px;
      margin-left: 0px;
      padding: 1.25rem 0;
  };
  cursor: pointer;
`
const StyledTitle = styled.div<PropsActive>`
  font-family: museo-sans, sans-serif;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: -0.05px;
  text-align: center;
  color:${props => props.active ? "#052948" : "#757575"};
`
interface Props {
  active: boolean,
  onPress: any,
  title: string
}
interface PropsActive {
  active: boolean,
}
export const ButtonItem: React.FC<Props> = (props) => {
  return (
    <Container active={props.active} onClick={() => { props.onPress() }}>
      <StyledTitle active={props.active}>
        {props.title}
      </StyledTitle>
    </Container>
  )
}
export default ButtonItem
